
.page {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.logo {
    height: 400px;
    margin-left: auto;
    margin-right: auto;
}

.tagline {
    font-weight: bold;
    color: #050D58;
}

@media screen and (max-width: 575px) {
    .logo {
        height: 350px;
    }
}

@media screen and (max-width: 500px) {
    .logo {
        height: 200px;
    }

    .tagline {
        font-size: 24px;
    }
}



