
.list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
}

.title {
    margin: 12px 0;
}

.listItem {
    background-color: #eaecfd;
    margin-bottom: 8px;
    padding: 8px 16px;
    -ms-flex-preferred-size: 95%;
        flex-basis: 95%;
    border-radius: 5px;
}

.link {
    text-decoration: none;
    font-size: 24px;
    color: #6771d1;
    -webkit-transition: color .7s;
    -o-transition: color .7s;
    transition: color .7s;
}

.link:hover {
    color: #fa656a;
    -webkit-transition: all .7s;
    -o-transition: all .7s;
    transition: all .7s;
    
}

.link:active {
    color: #fc3c40;
    -webkit-transition: all 0s;
    -o-transition: all 0s;
    transition: all 0s;
}

@media screen and (max-width: 550px) {
    .link {
        font-size: 20px;
    }
}


