
.footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 72px;
}

.link {
    text-decoration: none;
    color: #6771d1;
    -webkit-transition: color .7s;
    -o-transition: color .7s;
    transition: color .7s;
}

.link:hover {
    color: #fa656a;
    -webkit-transition: all .7s;
    -o-transition: all .7s;
    transition: all .7s;
    
}

.link:active {
    color: #fc3c40;
    -webkit-transition: all 0s;
    -o-transition: all 0s;
    transition: all 0s;
}




