.wrapper {
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: rgba(0,0,0,.5);
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 1;
    -webkit-transition: all .3s cubic-bezier(.51,.01,.73,.73);
    -o-transition: all .3s cubic-bezier(.51,.01,.73,.73);
    transition: all .3s cubic-bezier(.51,.01,.73,.73);
}

.wrapperHidden {
    opacity: 0;
    -webkit-transition: all .3s cubic-bezier(.51,.01,.73,.73);
    -o-transition: all .3s cubic-bezier(.51,.01,.73,.73);
    transition: all .3s cubic-bezier(.51,.01,.73,.73);
    z-index: 0;
}

.slider {
    position: fixed;
    top: 0;
    right: 0;
    width: 150px;
    height: 100%;
    background-color: #050d58;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding-top: 16px;
    -webkit-transition: -webkit-transform .3s cubic-bezier(.51,.01,.73,.73);
    transition: -webkit-transform .3s cubic-bezier(.51,.01,.73,.73);
    -o-transition: transform .3s cubic-bezier(.51,.01,.73,.73);
    transition: transform .3s cubic-bezier(.51,.01,.73,.73);
    transition: transform .3s cubic-bezier(.51,.01,.73,.73), -webkit-transform .3s cubic-bezier(.51,.01,.73,.73);
}

.putAway {
    -webkit-transform: translate(100%);
        -ms-transform: translate(100%);
            transform: translate(100%);
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}





