
.burger {
    color: #FFF;
    font-size: 32px;
    position: absolute;
    top: 28px;
    right: 20px;
    display: none;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.burger:active {
    color: #fc3c40;
    font-size: 40px;
    top: 23px;
    right: 16px;
    -webkit-transition-duration: all 0s;
         -o-transition-duration: all 0s;
            transition-duration: all 0s;
}

@media screen and (max-width: 550px) {
    .burger {
        display: block;
    }
}


